<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template v-slot="props">
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in props.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRightById(props.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for -->
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(props.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- for -->
                  <el-col :span="18">
                    <el-tag
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      type="warning"
                      closable
                      @close="removeRightById(props.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <!-- <pre>
                        {{props.row}}
                    </pre> -->
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template v-slot="props">
            <el-button type="primary" icon="el-icon-edit" size="mini"
              ></el-button
            >
            <el-button type="danger" icon="el-icon-delete" size="mini"
              ></el-button
            >
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRightsDialog(props.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightsVisible"
      width="50%"
    >
      <!--  -->
      <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" :default-expand-all="true"
      :default-checked-keys="defkeys"
      ref="rightsTreeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightsVisible = false"> </el-button>
        <el-button type="primary" @click="setRightsInfo"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      rolesList: [],
      // 
      setRightsVisible: false,
      // 
      rightsList: [],
      // 
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // Id
      defkeys: [105],
      // roleid
      currentRoleId: ''
    }
  },
  created () {
    // 
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolesList = res.data
      console.log(this.rolesList)
    },
    // 
    async removeRightById (role, rightId) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      console.log('confirm')
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      role.children = res.data
      this.$message.success('')
    },
    // ,
    async showSetRightsDialog (role) {
      // 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rightsList = res.data
      // console.log(res.data)
      // 3id
      const tempIds = []
      const children1 = role.children
      let idx1 = 0
      let idx2 = 0
      let idx3 = 0
      // console.dir(children1)
      while (children1[idx1]) {
        // console.log(1)
        var children2 = children1[idx1].children
        idx2 = 0
        idx1++
        while (children2[idx2]) {
          var children3 = children2[idx2].children
          idx3 = 0
          idx2++
          while (children3[idx3]) {
            tempIds.push(children3[idx3].id)
            idx3++
          }
        }
      }
      console.log(tempIds)
      this.defkeys = tempIds
      this.setRightsVisible = true
      this.currentRoleId = role.id
    },
    // api
    async setRightsInfo () {
      const keys = [
        ...this.$refs.rightsTreeRef.getCheckedKeys(),
        ...this.$refs.rightsTreeRef.getHalfCheckedKeys()
      ]
      const keysStr = keys.join(',')
      console.log(keysStr)
      const { data: res } = await this.$http.post(`roles/${this.currentRoleId}/rights`, { rids: keysStr })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
      this.setRightsVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #cccccc;
}
.bdbottom {
  border-bottom: 1px solid #cccccc;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
