<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="12">
          <el-input placeholder="" v-model="queryInfo.query"
            ><el-button
              slot="append"
              icon="el-icon-search"
              @click="getOrderList"
              clearable
              @clear="getOrderList"
            ></el-button
          ></el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column
          prop="order_number"
          label=""
          width="300px"
        ></el-table-column>
        <el-table-column
          prop="order_price"
          label=""
          width="50px"
        ></el-table-column>
        <el-table-column label="">
          <template v-slot="props">
            <el-tag v-if="props.row.order_pay !== '1'" type="danger"
              ></el-tag
            >
            <el-tag v-else type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column label="" width="150px">
          <template v-slot="props">
            {{ (props.row.create_time * 1000) | formatDate }}
          </template>
        </el-table-column>
        <el-table-column label="" width="120px">
          <template>
            <el-button
              type="primary"
              class="el-icon-edit"
              size="mini"
              @click="showEditDialog"
            ></el-button>
            <el-button
              type="success"
              class="el-icon-location"
              size="mini"
              @click="showKuaidi"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form
        v-model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="city">
          <el-cascader
            v-model="editForm.city"
            :options="cities"
            @change="handleCityChange"
            :props="cityProps"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="editForm.address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="kuadiDialogVisible" width="50%">

    </el-dialog>
  </div>
</template>

<script>
import cities from './citydata.js'
export default {
  data () {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0,
      // 
      orderList: [],
      // 
      editDialogVisible: false,
      // edit
      editForm: {
        address: '',
        city: []
      },
      //   
      editFormRules: {
        address: [
          { required: true, message: '', trigger: 'blur' }
        ],
        city: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      cities: cities,
      // 
      cityProps: {
        label: 'label',
        value: 'value',
        children: 'children'
      },
      // 
      kuadiDialogVisible: false,
      // 
      kuaidiInfo: []
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList () {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      }).catch(err => err)
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.total = res.data.total
      this.orderList = res.data.goods
      console.log('', res.data)
    },
    // pagesize
    handleSizeChange (newPagesize) {
      this.queryInfo.pagesize = newPagesize
      this.getOrderList()
    },
    // pagenum
    handleCurrentChange (newPagenum) {
      this.queryInfo.pagenum = newPagenum
      this.getOrderList()
    },
    // 
    async showEditDialog () {
      this.editDialogVisible = true
    },
    handleCityChange () {},
    // 
    async showKuaidi () {
      // 
      const { data: res } = await this.$http.get('kuaidi/1106975712662').catch(err => err)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('!')
      this.kuaidiInfo = res.data
      console.log(':', this.kuaidiInfo)
      this.kuadiDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
