<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button type="primary" @click="addUserVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userList" style="width: 100%" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label="" width="180">
        </el-table-column>
        <el-table-column prop="email" label="" width="180">
        </el-table-column>
        <el-table-column prop="mobile" label="" width="180">
        </el-table-column>
        <el-table-column prop="role_name" label="" width="180">
        </el-table-column>
        <el-table-column label="">
          <template v-slot="props">
            <el-switch
              v-model="props.row.mg_state"
              @change="userStateChanged(props.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template v-slot="props">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(props.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUserInfo(props.row.id)"
            ></el-button>
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="showSetRoleDialog(props.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addUserVisible"
        @close="addDialogClose"
        width="50%"
      >
        <!--  -->
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="70px"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addUserVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClose"
      >
        <el-form
          :model="editForm"
          :rules="editFormRules"
          ref="editFormRef"
          label-width="70px"
        >
          <el-form-item label="">
            <el-input v-model="editForm.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="editForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUser"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="setRoleDialogVisible"
        width="50%"
        @close="setRoleDialogClosed"
      >
        <div>
          <p>:{{ userInfo.username }}</p>
          <p>:{{ userInfo.role_name }}</p>
          <p>
            :
            <el-select v-model="roleId" placeholder="">
              <el-option
                v-for="item in rolesList"
                :key="item.id"
                :label="item.roleName"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="setRole"
            > </el-button
          >
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    // 
    const checkEmail = (rule, value, cb) => {
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (regEmail.test(value)) {
        // 
        return cb()
      }
      // 
      return cb(new Error(''))
    }
    // 
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^\d{11}$/
      if (regMobile.test(value)) {
        // 
        return cb()
      }
      return cb(new Error(''))
    }

    return {
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 2
      },
      // 
      userList: [],
      // 
      total: 0,
      // 
      addUserVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // d
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 10,
            message: '3~10',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            validator: checkEmail,
            trigger: ['blur', 'change']
          }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          {
            validator: checkMobile,
            trigger: 'blur'
          }
        ]
      },
      // 
      editDialogVisible: false,
      editForm: {},
      // 
      editFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            validator: checkEmail,
            trigger: ['blur', 'change']
          }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          {
            validator: checkMobile,
            trigger: 'blur'
          }
        ]
      },
      /// /
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // 
      rolesList: [],
      // Id
      roleId: ''
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    // ,
    async getUserList () {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.userList = res.data.users
      this.total = res.data.total
      console.log(this.userList)
    },
    // pagesize
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.queryInfo.pagenum = 1
      this.getUserList()
    },
    //   
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // 
    async userStateChanged (userinfo) {
      // console.log(userinfo)
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    addDialogClose () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser () {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) {
          return
        }
        // 
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addUserVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog (id) {
      // 
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      this.editDialogVisible = true
      console.log(this.editForm)
    },
    // 
    editDialogClose () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editUser () {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) {
          return
        }
        // 
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // 
    async deleteUserInfo (id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      //  confirm
      if (confirmResult === 'confirm') {
        const { data: res } = await this.$http.delete('users/' + id)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        this.$message.success('')
        // 
        this.getUserList()
      } else if (confirmResult === 'cancel') {
        //  cancel
        return this.$message.info('')
      }
    },
    // 
    async showSetRoleDialog (userInfo) {
      this.userInfo = userInfo
      // 
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolesList = res.data
      //   console.log(this.rolesList)
      this.setRoleDialogVisible = true
    },
    // 
    async setRole () {
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, { rid: this.roleId })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getUserList()
      this.setRoleDialogVisible = false
    },
    // 
    setRoleDialogClosed () {
      this.roleId = ''
      this.userInfo = ''
    }
  }
}
</script>

<style lang="less" scoped>
</style>>
